import { Component } from '../react';
import { setAttributes } from './dom';
import { unmountComponent,setComponentProps,createComponent } from './render';

//å¯¹æ¯”å½“å‰çœŸå®çš„DOMå’Œè™šæ‹ŸDOMï¼Œåœ¨å¯¹æ¯”è¿‡ç¨‹ä¸­ç›´æ¥æ›´æ–°çœŸå®DOM
//åªå¯¹æ¯”åŒä¸€å±‚çº§çš„å˜åŒ–

/*  
@param {HTMLELement} dom çœŸå®DOM
@param {vnode} vNode è™šæ‹ŸDOM
@param {HTMLElement} container çœŸå®å®¹å™¨DOM
@return {HTMLElement} æ›´æ–°åçœŸå®DOM
*/
//å¯¹æ¯”èŠ‚ç‚¹åŠå­èŠ‚ç‚¹å¹¶è¿åœ¨containerèŠ‚ç‚¹ä¸‹é¢
export function diff(dom,vNode,container) { 
    const result = diffNode(dom,vNode);
    if (container && result.parentNode !== container) {
        container.appendChild(result);
    }
    return result;
}

//@return {HTMLElement}
//å¯¹æ¯”èŠ‚ç‚¹åŠå­èŠ‚ç‚¹
//è‹¥èŠ‚ç‚¹ä¸ä¸€æ ·åˆ™ç›´æ¥åˆ é™¤æ—§èŠ‚ç‚¹å¹¶å°†å­èŠ‚ç‚¹å…¨éƒ¨è¿åœ¨æ–°ç”ŸæˆèŠ‚ç‚¹ä¸Šé¢
export function diffNode(dom,vNode) {
    let newDom = dom;
    if (vNode === undefined || vNode === null || typeof vNode === "boolean") {
        vNode = "";
    }
    if (typeof vNode === "number") {
        vNode = vNode.toString();
    }
    //å¯¹æ¯”text node
    if (typeof vNode === "string") {
        //domä¸ºæ–‡å­—ä¸”ä¸vNodeä¸åŒ
        if (dom && dom.nodeType === 3 && dom.textContent !== vNode) {
            dom.textContent = vNode;    
        } else {
            //è‹¥ä¹‹å‰DOMä¸æ˜¯æ–‡å­—èŠ‚ç‚¹åˆ™æ–°å»ºä¸€ä¸ªæ–‡å­—èŠ‚ç‚¹å¹¶åˆ é™¤æ—§èŠ‚ç‚¹
            newDom = document.createTextNode(vNode);
            if (dom && dom.parentNode) {
                dom.parentNode.replaceChild(newDom,dom);
            }
        }
        return newDom;
    }
    //å‡½æ•°æˆ–ç±»å‹ç»„ä»¶
    if (typeof vNode.tag === "function") {
        return diffComponent(dom,vNode);
    }
    //è‹¥vNodeä¸domçš„ç±»å‹ä¸ä¸€æ ·åˆ™ç”Ÿæˆä¸€ä¸ªæ–°domæ›¿æ¢åŸæ¥dom
    if(!dom || !isSameNodeType(dom,vNode)) {
        newDom = document.createElement(vNode.tag);
        if (dom) {
            //æŠŠæ‰€ä»¥å­èŠ‚ç‚¹(ä¸æ­¢åº•ä¸‹ä¸€å±‚)å…¨éƒ¨è¿åœ¨newDomä¸Š
            let children = [...dom.childNodes];
            children.map(child => newDom.appendChild(child));
            //æŠŠdomæ›¿æ¢æˆnewDomå¹¶ç§»é™¤dom
            if (dom.parentNode) {
                dom.parentNode.replaceChild(newDom,dom);
            }
        }
    }
    if((vNode.children && vNode.children.length > 0) || (newDom.childNodes && newDom.childNodes.length > 0)){
        diffChildren(newDom,vNode);
    }
    diffAttributes(newDom,vNode);
    return newDom;
}

//æ›´æ–°è‡ªå®šä¹‰ç»„ä»¶ 
//@return { HTMLELement }   
function diffComponent(dom,vNode) {
    let compoent = dom && dom._component;
    let oldDom = dom;
    //ç»„ä»¶ç±»å‹æ— å˜åŒ–,åˆ™å¯ä»¥åªæ›´æ–°props
    if (compoent && compoent.constructor === vNode.tag) {
        setComponentProps(compoent,vNode.attrs);
        dom = compoent.base;
    //ç»„ä»¶ç±»å‹æœ‰å˜åŒ–åˆ™ç§»é™¤åŸç»„ä»¶å¹¶æ¸²æŸ“æ–°ç»„ä»¶
    } else {
        if (compoent) {
            unmountComponent(compoent);
            oldDom = null;
        }
        compoent = createComponent(vNode.tag,vNode.attrs);
        setComponentProps(compoent,vNode.attrs);
        //å°†é‡æ–°ç”Ÿæˆçš„æ–°dom(component.base)èµ‹ç»™dom
        dom = compoent.base;
        //å¯¹æ¯”æ—§domä¸æ–°dom,å¹¶å†³å®šæ˜¯å¦ç§»é™¤æ—§dom
        if (oldDom && oldDom !== dom) {
            oldDom._component = null;
            removeNode(oldDom);
        }
    }
    return dom;
}

//å¯¹æ¯”å¹¶æ›´æ–°domèŠ‚ç‚¹çš„attributes
function diffAttributes(dom,vNode) {
    const domAttrs = {};
    const vNodeAttrs = vNode.attrs;
    for (let i = 0;i < dom.attributes.length;i++) {
        let attr = dom.attributes[i];
        domAttrs[attr.name] = attr.value;
    }
    for (let name in domAttrs) {
        //è‹¥åŸå±æ€§ä¸åœ¨vNodeä¸­,åˆ™ç½®ä¸ºnull
        //è‹¥å±æ€§å€¼ä¸åŒåˆ™æ›´æ–°ä¸ºvNodeçš„å±æ€§
        if(!(name in vNodeAttrs)) {
            setAttributes(dom,name,null);
        }
    }
    for (let name in vNodeAttrs) {
        if (domAttrs[name] !== vNodeAttrs[name]) {
            setAttributes(dom,name,vNodeAttrs[name]);
        }
    }
}

//æ›´æ–°å­èŠ‚ç‚¹,å­èŠ‚ç‚¹ä¸ºæ•°ç»„æœ‰keyå€¼
function diffChildren(dom,vNode) {
    const domChildren = dom.childNodes;
    const vNodeChildren = vNode.children;
    const map = {};//æœ‰keyçš„èŠ‚ç‚¹,{key:childNode}
    const noKey = [];//æ²¡æœ‰keyçš„èŠ‚ç‚¹
    if (domChildren.length > 0) {
        //æœ‰keyå€¼çš„æ”¾å…¥mapä¸­,æ²¡æœ‰çš„æ”¾å…¥noKeyæ•°ç»„ä¸­
        for (let i = 0;i < domChildren.length;i++) {
            let child = domChildren[i],key = child.key;
            if (key) {
                map[key] = child;
            } else {
                noKey.push(child);
            }
        }
    }
    
    if (vNodeChildren && vNodeChildren.length > 0) {
        let min = 0;
        let noKeyLen = noKey.length;
        for (let i = 0;i < vNodeChildren.length;i++) {
            let vChild = vNodeChildren[i],key = vChild.key;
            let child;
            //æœ‰key,æ‰¾åˆ°å¯¹åº”mapä¸­çš„å®é™…DOMèŠ‚ç‚¹
            if (key) {
                if (map[key]) {
                    child = map[key];
                    map[key] = null;
                }
            //æ²¡æœ‰key,åœ¨noKeyæ•°ç»„ä¸­æ‰¾ä¸€ä¸ªç›¸åŒç±»å‹çš„DOMèŠ‚ç‚¹
            } else if (min < noKeyLen) {
                console.log(min,noKeyLen);
                for (let j = min;j < noKeyLen;j++) {
                    let component = noKey[j];
                    if (component && isSameNodeType(component,vChild))  {

                        child = component;
                        noKey[j] = null;
                        if (j === noKeyLen - 1) noKeyLen--;
                        if (j === min) min++;
                        break;
                    } 
                }
            }
            //æ›´æ–°å­èŠ‚ç‚¹çœŸå®DOM
            child = diff(child,vChild);
            let domChild = domChildren[i];
            if (child && child !== dom && child !== domChild) {
                //å¦‚æœæ— æ›´æ–°å‰èŠ‚ç‚¹,åˆ™æ­¤èŠ‚ç‚¹ä¸ºæ–°å¢
                if (!domChild) {
                    dom.appendChild(child);
                //è‹¥æ›´æ–°åèŠ‚ç‚¹ä¸æ›´æ–°å‰ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸€æ ·
                } else if (child === domChild.nextSibling) {
                    removeNode(domChild);
                //å°†æ›´æ–°åèŠ‚ç‚¹ç§»åŠ¨
                } else {
                    dom.insertBefore(child,domChild);
                }
            }
        }
    }
}

//æŸ¥çœ‹çœŸå®DOMä¸è™šæ‹Ÿçš„æ˜¯å¦ä¸ºåŒç±»å‹
//@param {HTMLElement}dom çœŸå®DOM
//@param {vnode} vNode è™šæ‹ŸDOM
//@return {boolean}
function isSameNodeType(dom,vNode) {
    if (typeof vNode === "string" || typeof vNode === "number") {
        return dom.nodeType === 3;
    }
    //åŸç”Ÿç»„ä»¶å¯¹æ¯”
    if (typeof vNode.tag === "string") {
        return dom.nodeName.toLowerCase() === vNode.tag.toLowerCase();
    }
    //è‡ªå®šä¹‰ç»„ä»¶å¯¹æ¯”,dom._componentæ˜¯çœŸå®DOMå¯¹åº”çš„instance
    return dom && dom._component && dom._component.constructor === vNode.tag;
}

//ç§»é™¤å®é™…DOM
function removeNode(dom) {
    if (dom && dom.parentNode) {
        dom.parentNode.removeChild(dom);
    }
}


