export function setAttributes(dom,key,value) {
    //æ›¿æ¢classNameä¸ºclass
    if (key === "className") {
        key = "class";
    }
    //keyä¸ºonXXXXçš„ä¸ºäº‹ä»¶
    if (/on\w+/.test(key)) {
        key = key.toLowerCase();//onClick => onclick
        dom[key] = value || "";
    } else if (key === "style") {//æ›´æ–°style
        if (!value || typeof value === "string") {
            dom.style.cssText = value || "";
        }else if (value && typeof value === "object") {
            for (let name in value) {
                dom.style[name] = typeof value[name] === "number" ? value[name] + "px":value[name];
            }
        }
    }else {//æ™®é€šå±æ€§,å¯ä»¥ç›´æ¥æ›´æ–° 
        if (key in dom) {
            dom[key] = value;
        }
        if (value) {
            dom.setAttribute(key,value);//real dom api
        } else {
            dom.removeAttribute(name);
        }
    }
}