import { renderComponent } from '../react-dom/render';

//å¼‚æ­¥è¿›è¡ŒsetState,ä½¿å¾—setStateä¸è‡³äºå¤ªè¿‡é¢‘ç¹
const queue = [];
const renderQueue = [];//æ¸²æŸ“ç»„ä»¶é˜Ÿåˆ—,ä¸ä¼šæœ‰é‡å¤ç»„ä»¶

//å»¶è¿Ÿæ‰§è¡Œfn
function defer(fn) {
    return Promise.resolve().then(fn);
}

export function enqueueSetState (stateChange,component){
    if (queue.length === 0) {
        defer(flush);
    }
    queue.push({stateChange,component});
    //è‹¥renderQueueä¸­æ²¡æœ‰componentæ‰å°†å…¶æ”¾å…¥renderQueue
    if (!renderQueue.some(item => item === component)) {
        renderQueue.push(component);
    }
}


function flush() {
    let component;
    //å°†queueä¸­çš„setStateæ•´åˆèµ·æ¥
    while (queue.length !== 0) {
        const { stateChange,component } = queue.shift();
        //è‹¥æ²¡æœ‰prevStateåˆ™åˆå§‹åŒ–prevStateä¸ºå½“å‰state
        if (!component.prevState) {
            component.prevState = Object.assign({},component.state);
        }
        //è‹¥setStateä¼ å…¥ä¸ºå‡½æ•°
        if (typeof stateChange === "function") {
            Object.assign(component.state,stateChange(component.prevState,component.state));
        //è‹¥setStateä¼ å…¥ä¸ºå¯¹è±¡
        } else {
            Object.assign(component.state,stateChange);
        }
        component.prevState = component.state;
    }
    //ä¾æ¬¡æ¸²æŸ“component
    while (renderQueue.length !== 0) {
        component = renderQueue.shift();
        renderComponent(component);
    }
}